<template>
  <div class="add-blog" id="edit-blog">
    <div class="title">
      <h1>编辑博客</h1>
    </div>
    <div class="content">
      <form v-if="!isSubmitted">
        <div class="wrapper">
          <label>主题：</label>
          <input type="text" v-model="blog.title" value="title">
        </div>

        <div class="wrapper main-content">
          <label>内容：</label>
          <textarea name="blog.content" id="" cols="30" rows="10" v-model="blog.content"></textarea>
        </div>

        <div class="wrapper category">
          <label>分类：</label>
          <ul>
            <li>
              <label>Vue.js</label>
              <input type="checkbox" v-model="blog.categories" value="Vue.js">
            </li>
            <li>
              <label>React.js</label>
              <input type="checkbox" v-model="blog.categories" value="React.js">
            </li>
            <li>
              <label>Node.js</label>
              <input type="checkbox" v-model="blog.categories" value="Node.js">
            </li>
            <li>
              <label>Angular4</label>
              <input type="checkbox" v-model="blog.categories" value="Angular4">
            </li>
          </ul>
        </div>
        <div class="wrapper">
          <label>作者：</label>
          <input type="text" name="author" value="blog.author" v-model="blog.author">
        </div>
        <!--提交事件而不重载页面-->
        <button class="button-warn" v-on:click.prevent="update()">确认修改</button>
      </form>

    </div>

    <div class="suc" v-if="isSubmitted">
      <span>您的博客修改成功，感谢。</span>
    </div>

    <!--分割线-->
    <div class="line"></div>
    <div class="preview">
        <div class="title-wrapper">
          <h3>博客预览</h3>
        </div>
        <div class="content">
          <span><i>主题：</i>{{ blog.title }}</span>
          <span><i>内容：</i><br>{{ blog.content }}</span>
          <span><i>分类：</i>
              <ul>
                <li v-for="category in blog.categories">{{ category }}&nbsp;/&nbsp;</li>
              </ul>
          </span>
          <span><i>作者：</i>{{ blog.author }}</span>
        </div>

    </div>
  </div>
</template>

<script>
  import Axios from 'axios'

  export default {
    name: 'edit-blog',
    data() {
      return {
        id: this.$route.params.id,
        blog: {
          title: '',
          content: '',
          author: '',
          categories: [],
        },
        isSubmitted: false
      }
    },
    methods: {
      update: function () {
        var dialogue = confirm("确认修改?");
        if (dialogue === true) {
          Axios.put('/posts/' + this.id +'.json', this.blog)
            .then((res) =>{
              this.isSubmitted = true;
              console.log(this.blog)
            })
        } else {
          return false;
        }
      },
      fetchData: function () {
        Axios.get('/posts/' + this.id +'.json')
          .then((res) => {
            this.blog = res.data;

            // 如果分类只有一个，则categories的类型会变成object！这是要将其转换为数组，才可正常使用
            if (res.data.categories.length === undefined) {
              this.blog.categories = [res.data.categories[0]];
            }
          })
      }
    },
    created: function () {
      this.fetchData();
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less" src="../AddBlog/AddBlog.less"></style>



